        // Photo jQury filter 

$(function() {
    $('.button-box button').click(function() {
        var getId = this.id;
        var getCurrent = $('.gallery-block .' + getId);
        
        $('.button-box button').removeClass('active');
        $(this).addClass('active');

        if (getId == 'showall') {
            $('.gallery-item').addClass('lb').show(500);
        } else {
            $('.gallery-item').not(getCurrent).removeClass('lb').hide(500);
            getCurrent.addClass('lb').show(500);
        };
    });

        // Lightbox gallery

        var $li = $('.gallery-block').find('> li'),
        $links = $li.find('> a'),
        $lightbox = $('.lightbox'),
        $next = $('.next'),
        $prev = $('.prev'),
        $overlay = $('.overlay'),
        liIndex,
        targetImg;

    function replaceImg(src) {
        $lightbox.find('img').attr('src', src);
    }

    function getHref(index) {
        return $li.eq(index).find('>a').attr('href');
    }

    function closeLigtbox() {
        $lightbox.fadeOut();
    }

    $overlay.click(closeLigtbox);
    $('body').keydown(function (e) {
        if (e.keyCode == 27) {
            closeLigtbox();
        }
    });

    $links.click(function (e) {
        e.preventDefault();
        targetImg = $(this).attr('href');
        liIndex = $(this).parent().index();
        replaceImg(targetImg);
        $lightbox.fadeIn();
    });

    //$next.click(function () {
    //    if ((liIndex + 1) < $li.length) {
    //        targetImg = getHref(liIndex + 1);
    //        liIndex++;
    //    } else {
    //        targetImg = getHref(0);
    //        liIndex = 0;
    //    }
    //    replaceImg(targetImg);
    //});

    //$prev.click(function () {
    //    if ((liIndex) > 0) {
    //        targetImg = getHref(liIndex - 1);
    //        liIndex--;
    //    } else {
    //        targetImg = getHref($li.length - 1);
    //        liIndex = $li.length - 1;
    //    }
    //    replaceImg(targetImg);
    //});

    $next.click(function () {
        var next = $li.eq(liIndex).nextAll(':visible').eq(0);
        liIndex = next.length ? next.index() : $li.filter(':visible').first().index();
        replaceImg(getHref(liIndex));
    });

    $prev.click(function () {
        var prev = $li.eq(liIndex).prevAll(':visible').eq(0);
        liIndex = prev.length ? prev.index() : $li.filter(':visible').last().index();
        replaceImg(getHref(liIndex));
    });

});

        //Burger mobile menu

(function ($) {
    $(function () {
        $('.burger').on('click', function () {
            $(this).parent().find('ul').toggleClass('menu-open');
            $(this).toggleClass('menu-open');
        });
    });
})(jQuery);